import {useEffect, useState} from "react";
import numberGroupToArray from "./util/numberGroupToArray";

const useDay4 = (initialValue: string) => {
    const [camps, setCamps] = useState<string>(initialValue)
    const [exclusivePairs, setExclusivePairs] = useState<number>(0)
    const [partialPairs, setPartialPairs] = useState<number>(0)

    useEffect(() => {
        const pairs = camps.split(/\n/);

        const overlaps = pairs.map((line) => {
            const [elf1 = [], elf2 = []] = line.split(',').map(numberGroupToArray)
            const hasContent = (elf1.length > 0 && elf2.length > 0)
            const exclusive = hasContent ? elf1.every((d) => elf2.includes(d)) || elf2.every((d) => elf1.includes(d)) : false;
            const partial = elf1.some((d) => elf2.includes(d));
            return { exclusive, partial }
        })

        setExclusivePairs(overlaps.filter(({ exclusive }) => exclusive).length)
        setPartialPairs(overlaps.filter(({ partial }) => partial).length)
    }, [camps])

    return {
        setCamps, exclusivePairs, partialPairs
    }
}

export {useDay4}